
































import AsModal from "@/mixins/AsModal";
import {Component, Emit, Mixins} from "vue-property-decorator";
import {format} from "date-fns";

@Component
export default class AddProjectModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}

  @Emit("add")
  add(sequence: any) {
    return sequence;
  }

  save() {
    this.add({...this.formData, created: format(new Date(), "YYYY/MM/DD")});
    this.clean();
  }

  clean(): void {
    this.formData = {name: "", sequenceName: ""};
    this.dismiss();
  }
  formData = {name: "", sequenceName: ""};

  sequenceOptions = [
    {
      label: "Global Sequence",
      options: [
        {
          value: "GS 1",
          label: "GS 1",
        },
      ],
    },
    {
      label: "Pal Sequence",
      options: [
        {
          value: "PS A",
          label: "PS A",
        },
        {
          value: "PS B",
          label: "PS B",
        },
      ],
    },
    {
      label: "Your Sequence",
      options: [
        {
          value: "YS 1",
          label: "Ys 1",
        },
        {
          value: "YS 2",
          label: "YS 2",
        },
        {
          value: "YS 3",
          label: "YS 3",
        },
      ],
    },
  ];
}
