

















































import AddOTAModal from "@/components/ota/AddOTAModal.vue";
import {OTA} from "@/types/ApiTypes";
import {Component, Vue} from "vue-property-decorator";

@Component({
  components: {AddOTAModal},
})
export default class OTAView extends Vue {
  displayAddModal: boolean = false;
  data: OTA[] = [];

  mounted(): void {
    this.getData();
  }

  getData(): void {
    this.api.getOTAList().then((res) => {
      this.data = res;
    });
  }
}
