import {ApiCaller} from "@/core/api/ApiCaller";
import {ApiConfig} from "@/core/api/ApiConfig";
import {asPromised} from "@/core/api/asPromised";
import {initialOTA} from "@/core/constants/api/initialOTA";
import {initialPlanManagement} from "@/core/constants/api/initialPlanManagement";
import {LoginResponse} from "@/types/api/response/LoginResponse";
import {UserAccountManagement} from "@/types/api/response/UserAccountManagement";
import {
  Account,
  DeviceManagement,
  Dm,
  JSONWebToken,
  Lab,
  OTA,
  OTASerializerWithoutForeignKey,
  PlanM,
  PlanManagement,
  TransducerDB,
  Uam,
} from "@/types/ApiTypes";

export class ApiExecutor {
  private readonly config: ApiConfig;
  private readonly caller: ApiCaller;

  constructor(config: ApiConfig) {
    this.config = config;
    this.caller = new ApiCaller(config);
  }

  // api-token-auth

  public login(params: JSONWebToken): Promise<LoginResponse> {
    return asPromised<LoginResponse>(
      this.caller.post<JSONWebToken, LoginResponse>("/api-token-auth/", params),
      {token: "", status: 0},
      true,
    );
  }

  // Plan Management

  public getPlanList(params?: PlanM): Promise<PlanManagement[]> {
    return asPromised<PlanManagement[]>(this.caller.get<PlanM | null, PlanManagement[]>("/PlanM/", params), [], true);
  }

  public addPlanManagement(params: PlanManagement): Promise<PlanManagement> {
    return asPromised<PlanManagement>(
      this.caller.post<PlanManagement, PlanManagement>("/PlanM/", params),
      initialPlanManagement(),
      true,
    );
  }

  public getPlanManagement(id: number): Promise<PlanManagement> {
    return asPromised<PlanManagement>(
      this.caller.get<{}, PlanManagement>("/PlanM/" + `${id}/`, {}),
      initialPlanManagement(),
      true,
    );
  }

  public putPlanManagement(params: PlanManagement, id: number): Promise<PlanManagement> {
    return asPromised<PlanManagement>(
      this.caller.put<PlanManagement, PlanManagement>("/PlanM/" + `${id}/`, params),
      initialPlanManagement(),
      true,
    );
  }

  public deletePlanManagement(id: number): Promise<any> {
    return asPromised<any>(this.caller.delete<PlanManagement, any>("/PlanM/" + `${id}/`, {}), null, true);
  }

  // ota

  public getOTAList(): Promise<OTA[]> {
    return asPromised<OTA[]>(this.caller.get<{}, OTA[]>("/ota/", {}), [], true);
  }

  public addOTA(params: OTASerializerWithoutForeignKey): Promise<OTASerializerWithoutForeignKey> {
    return asPromised<OTASerializerWithoutForeignKey>(
      this.caller.post<OTASerializerWithoutForeignKey, OTASerializerWithoutForeignKey>("/ota/", params),
      initialOTA(),
      true,
    );
  }

  // uam

  public getUserAccountList(params: Uam): Promise<UserAccountManagement[]> {
    return asPromised<UserAccountManagement[]>(
      this.caller.get<Uam, UserAccountManagement[]>("/uam/", params),
      [],
      true,
    );
  }

  public addUserAccount(params: Partial<Account>): Promise<Account> {
    return asPromised<Account>(this.caller.post<Partial<Account>, Account>("/uam/", params), null, true);
  }

  // dm

  public getDeviceList(params: Dm): Promise<DeviceManagement[]> {
    return asPromised<DeviceManagement[]>(this.caller.get<Dm, DeviceManagement[]>("/dm/", params), [], true);
  }

  public addDeviceManagement(params: DeviceManagement): Promise<DeviceManagement> {
    return asPromised<DeviceManagement>(
      this.caller.post<DeviceManagement, DeviceManagement>("/dm/", params),
      null,
      true,
    );
  }

  public getDeviceManagement(id: number): Promise<DeviceManagement> {
    return asPromised<DeviceManagement>(
      this.caller.get<{}, DeviceManagement>("/dm/" + `${id}/`, {}),
      initialPlanManagement(),
      true,
    );
  }

  public deleteDeviceManagement(id: number): Promise<any> {
    return asPromised<any>(this.caller.delete<{}, any>("/dm/" + `${id}/`, {}), null, true);
  }

  public putDeviceManagement(params: DeviceManagement, id: number): Promise<DeviceManagement> {
    return asPromised<DeviceManagement>(
      this.caller.put<DeviceManagement, DeviceManagement>("/dm/" + `${id}/`, params),
      initialPlanManagement(),
      true,
    );
  }

  // lab

  public getLabList(params: Uam): Promise<Lab[]> {
    return asPromised<Lab[]>(this.caller.get<Uam, Lab[]>("/lab/", params), [], true);
  }

  public addLab(params: Lab): Promise<Lab> {
    return asPromised<Lab>(this.caller.post<Lab, Lab>("/lab/", params), null, true);
  }

  // tdb


  public getTDBList(params: Uam): Promise<TransducerDB[]> {
    return asPromised<TransducerDB[]>(this.caller.get<Uam, TransducerDB[]>("/tdb/", params), [], true);
  }

  public addTransducerDB(params: TransducerDB): Promise<TransducerDB> {
    return asPromised<TransducerDB>(this.caller.post<TransducerDB, TransducerDB>("/tdb/", params), null, true);
  }
}
