import {PermissionStorage} from "@/storages/PermissionStorage";
import {Permission, PERMISSION_MUTATION, PermissionActions, PermissionMutations} from "@/types/store/PermissionStore";
import {VuexActions, VuexMutations} from "@/types/store/VuexTypes";
import {GetterTree} from "vuex";

const namespaced = true;

// status: 0: user, 1: admin, 2: root
const state: Permission = {
  status: 0,
  token: "",
};

const getters: GetterTree<Permission, any> = {
  status: ({status}) => status,
  token: ({token}) => token,
};

const mutations: VuexMutations<PermissionMutations, Permission> = {
  UPDATE_PERMISSION(store, permission): void {
    store.status = (permission as Permission).status;
    store.token = (permission as Permission).token;
  },
  CLEAR(permission): void {
    permission.status = 0;
    permission.token = "";
  },
};

const actions: VuexActions<PermissionActions, Permission, any> = {
  UPDATE_PERMISSION: (context, permission) => {
    context.commit(PERMISSION_MUTATION.UPDATE_PERMISSION, permission);

    Object.keys(permission as Permission).forEach((key) => {
      // @ts-ignore
      PermissionStorage.set(key, permission[key]);
    });
  },
  CLEAR(context): void {
    context.commit(PERMISSION_MUTATION.CLEAR);
    PermissionStorage.clear();
  },
};

export default {
  namespaced,
  state,
  getters,
  actions,
  mutations,
};
