





























import {Lab} from "@/types/ApiTypes";
import {Component, Vue} from "vue-property-decorator";
import {TABS_ACTION, TabsActions} from "@/types/store/TabStore";
import {Action} from "vuex-class";

@Component
export default class MyLabsView extends Vue {
  @Action(TABS_ACTION.UPDATE_CURRENT_TAB)
  updateTab!: TabsActions["UPDATE_CURRENT_TAB"];

  goToTab(tabName: string): void {
    this.$router.push({query: {tab: tabName}});
    this.updateTab(tabName);
  }

  data: Lab[] = [];
  mounted(): void {
    this.getData();
  }

  getData(): void {
    this.api.getLabList(null).then((res) => {
      this.data = res;
    });
  }

  // TODO: fix
  typeClass(index: string): string {
    const result = Number(index) % 3;
    console.log(result);
    return result > 2 ? "danger" : result > 1 ? "warn" : "normal";
  }
}
